import React from "react";
import { NavLink } from "react-router-dom";

import "./Menu.css";

const Menu = () => (
  <ul className="menu">
    <li>
      <NavLink exact to="/">
        Главная
      </NavLink>
    </li>
    <li className="li">
      <NavLink to="/sessons">Времена года</NavLink>
    </li>
  </ul>
);

export default Menu;



