import React from "react";



import "./Buttons.css";

const Buttons = props => {


  const { buttons } = props;

  return <div>
      <button onClick={buttons}  value="spring">
        Весна{" "}
      </button>
      <button onClick={buttons} value="summer">
        Лето{" "}
      </button>
      <button onClick={buttons} value="autumn">
        Осень{" "}
      </button>
      <button onClick={buttons} value="winter">
        Зима{" "}
      </button>
    </div>;
};
export default Buttons;



